.leaderboard-card {
  min-height: 20vh;
  width: 50%;
  background-color: #2a2b2f;
  margin: 1.5rem;
  padding: 2rem;
  border-radius: 2rem;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
  transition: all 0.2s;
  display: flex;
  position: relative;
}

.leaderboard-card:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}

.card-1 {
  background: #16222a; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #3a6073,
    #16222a
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #3a6073,
    #16222a
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.stats {
  width: 100%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 2rem;
  margin-left: 5rem;
  line-height: 1.5;
}

.bg-num {
  font-size: 12rem;
  position: absolute;
  color: black;
  opacity: 0.2;
  top: 10px;
  left: 25px;
}

.card-name-lb {
  font-size: 2rem;
  font-weight: 900;
}

.player-img {
  height: 190px;
  width: 190px;
  border-radius: 100%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

.playerImg {
  display: flex;
  align-items: center;
  justify-content: center;
}

.lb-container {
  display: flex;
  width: 100%;
}

.lb-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 70%;
  height: 100%;
  background: rgba(255, 255, 255, 0.03);
  clip-path: polygon(0 0, 100% 0%, 68% 100%, 0% 100%);
}

.three-stats {
  display: flex;
  align-items: center;
  justify-content: center;
}

.lb-one-stat {
  display: flex;
  flex-direction: column;
  margin: 1rem;
  text-align: center;
}

.lb-one-stat h2 {
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 600;
}

@media screen and (max-width: 960px) {
  .leaderboard-card {
    width: 90vw;
    padding: 1rem;
  }

  .player-img {
    height: 70px;
    width: 70px;
  }

  .bg-num {
    font-size: 8rem;
  }

  .stats {
    padding: 0.6rem;
    margin-left: 4rem;
  }

  .card-name-lb {
    font-size: 1rem;
  }

  .lb-one-stat {
    margin: 0.3rem;
  }

  .lb-one-stat h2 {
    font-size: 0.5rem;
  }
}
