* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

body {
  max-width: 90%;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background: #db686f;
}

::-webkit-scrollbar-thumb:hover {
  background: #df2935;
  transition: all 0.2s ease;
}
