body {
  background-color: rgb(220, 223, 228);
  max-width: 100%;
  overflow-x: hidden;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  min-height: 87vh;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.main-committee-card {
  background-color: #fff;
  padding: 3rem;
  max-width: 60%;
  min-height: 80%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main-committee-card > h2 {
  font-size: 2.5rem;
  margin-bottom: 0.6rem;
}

.main-committee-card > h3 {
  color: #df2935;
  font-size: 1.5rem;
  margin-bottom: 1rem;
  font-style: italic;
}

.main-committee-card > p {
  color: #080708;
  line-height: 1.3;
  margin-bottom: 0.3rem;
  font-size: 1rem;
}

.commitee-img {
  height: 190px;
  width: 190px;
  border-radius: 100%;
  box-shadow: 9px -5px 0px 0px #df2935;
  margin-bottom: 1rem;
}

.commitee-img:hover {
  transform: scale(1.1);
  transition: all 0.1s ease-in;
}

.commitee-btns button {
  outline: none;
  border: none;
  padding: 1rem;
  margin: 1rem;
  background-color: white;
  font-size: 2rem;
  color: #db686f;
}

.commitee-btns button:hover {
  color: #df2935;
  transition: all 0.2s ease;
}

@media screen and (max-width: 768px) {
  .main-committee-card > h2 {
    font-size: 2rem;
  }

  .main-committee-card > h3 {
    font-size: 1.2rem;
  }
  .main-committee-card > p {
    font-size: 0.7rem;
  }

  img {
    height: 150px;
    width: 150px;
  }

  .commitee-btns button {
    font-size: 1rem;
    margin: 0rem;
  }
}

@media screen and (max-width: 960px) {
  .main-committee-card {
    max-width: 70%;
    padding: 1.5rem;
  }
}
